.mainfork {
  margin: 0 20px;
  position: relative;
  height: calc(100vh - 45px);
}

.mainfork-wrapper > button {
  margin-bottom: 12px;
}

.mainfork-wrapper {
  width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.mainfork-wrapper .FormField  {
  margin: 0 !important;
}

.jobcell {
    background: #fff;
    border-bottom: 1px solid #ddd;
    box-sizing: border-box;
    padding: 5px 10px;
}

.jobcell p {
    margin: 0;
}

.jobcell__head p {
    display: flex;
    justify-content: space-between;
    font-size: 14px;
    margin-bottom: 10px;
}

.jobcell__head--name {
    color: #0B9ED0;
    font-weight: 600;
}

.jobcell__head--salary {
    color: #444;
    font-weight: 600;
}

.jobcell__subhead--organization {
    font-size: 12px;
    color: #777;
}

.jobcell__subhead {
    margin-bottom: 10px;
}

.jobcell__subhead--second {
    display: flex;
    justify-content: space-between;
}

.jobcell__subhead--second-city {
    font-size: 12px;
    color: #777;
}

.jobcell__subhead--second-created {
    font-size: 12px;
    color: #444;
}

.jobcell__body {
    font-size: 13px;
    line-height: 1.2;
}
.extendedfilters__search {
    text-align: right;
    margin-bottom: 10px;
    padding-right: 16px;
}

.extendedfilters__search button {
    padding: 8px 36px;
    font-size: 14px;
    cursor: pointer;
    border: none;
    background: #0E81D6;
    color: #fff;
    font-weight: 500;
    border-radius: 2px;
    outline: none;
}
.jobpage {
    padding: 5px 10px;
}

.jobpage p, .jobpage h3 {
    margin: 0;
    margin-bottom: 10px;
}

.jobpage p {
    font-size: 14px;
}

.jobpage__name {
    font-size: 18px;
    color: #0B9ED0;
    font-weight: 600;
}

.jobpage__salary {
    font-size: 15px;
    color: #444;
    font-weight: 600;
}

.jobpage .jobpage__organization {
    color: #85144b;
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 5px;
}

.jobpage .jobpage__created {
    margin-bottom: 0;
    color: #777;
    font-size: 12px;
} 

.jobpage__address {
    font-size: 14px;
    color: #444;
}

.jobpage .jobpage__expirience {
    margin-bottom: 0;
}

.jobpage__expirience, .jobpage__employmentType {
    color: #444;
    font-size: 14px;
}

.jobpage__employmentType-value, .jobpage__expirience-value {
    font-weight: 600;
}

.jobpage h4 {
    color: #444;
    font-size: 15px;
}

.jobpage .jobpage__description {
    font-size: 14px;
    margin-top: 20px;
}

.jobpage__answer {
    text-align: right;
    margin-bottom: 10px;
}

.jobpage__answer button {
    padding: 8px 12px;
    cursor: pointer;
    font-size: 14px;
    border: none;
    background: #0E81D6;
    color: #fff;
    font-weight: 500;
    border-radius: 2px;
    outline: none;
}

